.overlay {
  background: #000000;
  bottom: 0;
  right: 0;
  left: 0;
  opacity: 0;
  position: fixed;
  top: 0;
  transition: opacity 0.25s ease-in, visibility 0.25s ease-in;
  visibility: hidden;
  z-index: 10;
}
.overlay--active {
  opacity: 0.2;
  transition: opacity 0.2s ease-out, visibility 0.2s ease-out;
  visibility: visible;
}